<template>
  <div class="main_navs">
    <Header></Header>

    <div class="container">
      <div class="login">
        <div class="row">
          <div class="col-xl-5">
            <div class="log_img">
              <div class="log_img_title">{{$t('reg')}}</div>
              <img src="../../../assets/images/llaptop.png" alt="">
            </div>
          </div>
          <div class="col-xl-7">
            <div class="log_form">
              <div>
                <form @submit.prevent="regIstration()">
                  <label>{{$t('email')}} *</label>
                  <div class="input_text">
                    <input type="email" v-model="email" placeholder="Email">
                  </div>
                  <label>{{$t('password')}}*</label>
                  <div class="input_text">
                    <input type="password" v-model="password" :placeholder="$t('password')">
                  </div>
                  <div class="forgot_password">
                    <router-link to="/" class="decoration">
                      {{$t('forget_password')}}
                    </router-link>
                  </div>
                  <div class="log_btn">
                    <button> {{$t('reg')}}</button>
                  </div>
                  <div class="phone_link">
                    <router-link to="/phone_reg" >
                      <div>
                        {{$t('phone_rg')}}
                      </div>
                    </router-link>
                  </div>
                </form>
                <div class="reg_links">
                  <router-link to="/email-login">
                    <div>{{$t('reg_quiz')}}? <span>{{$t('login')}}</span></div>
                  </router-link>
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";
export default {
  components: {Footer, Header},
  data(){
    return{
      email:'',
      password:''
    }
  },
  methods:{
    regIstration(){

      this.$axiosDefault.post('/students/register/',{
        "email":this.email,
        "password":this.password
      })
      .then(()=>{
        this.$toast("Emailga xabar jo'natildi iltimos tekshirib ko'ring", {
          timeout: 10000,
          type:"success"
        });
      })
      .catch(error=>{
        console.log(error)
        this.$toast(`${error.response.data.email},${error.response.data.password}`, {
          timeout: 10000,
          type:"error"
        });
      })

    }
  }

}
</script>
